<script>
import { useAccountStore, useTemplateStore } from '@/store'
import { mapState } from 'pinia'

import debug from 'debug'

import { CredentialState } from '../../constants/CredentialStates'

import IconAdd from '@/assets/icon-add.vue'
import IconApprovedCredentials from '@/assets/icon-approved-credentials.vue'
import IconIssuedCredentials from '@/assets/icon-issued-credentials-blue.vue'
import IconRevokedCredentials from '@/assets/icon-revoked-credentials.vue'
import emptyTablePhoto from '@/assets/no-pending-tasks.png'
import DataTable from '@/components/DataTable'
import {
  GET_CA,
  GET_TEMPLATE,
} from '@/store/actions'

const logDebug = debug('ViewTemplate')

export default {
  name: 'ViewTemplate',
  title: 'pageTitle.viewTemplate',
  components: {
    DataTable,

    IconAdd,
    IconRevokedCredentials,
    IconApprovedCredentials,
    IconIssuedCredentials,
  },
  props: {
    tid: {
      required: true,
    },
  },
  data() {
    return {
      loadingTable: true,
      tab: 0,
      issueCertificateModal: false,
      shownInBulkModal: false,
      userData: {},
      search: '',
      hasPendingApproval: false,
      allEntriesTable: [],
      pendingTable: [],
      approvedTable: [],
      revokedTable: [],
      cardHeaders: [],
      emptyTablePhoto,
      message: '',
      successModal: false,
      credentialsHeaders: {
        en: [
          {
            text: 'Public Attr',
            align: 'start',
            value: 'public_field.value',
          },
          { text: 'Credential ID', value: 'id' },
          {
            text: 'Status',
            value: 'status',
            sortable: false,
            filterable: false,
          },
          {
            text: 'Actions',
            value: 'actions',
            sortable: false,
            filterable: false,
          },
        ],
        pt: [
          {
            text: 'Atributo público',
            align: 'start',
            value: 'public_field.value',
          },
          { text: 'ID de Credencial', value: 'id' },
          {
            text: 'Estado ',
            value: 'status',
            sortable: false,
            filterable: false,
          },
          {
            text: 'Acções',
            value: 'actions',
            sortable: false,
            filterable: false,
          },
        ],
        es: [
          {
            text: 'Atributo público',
            align: 'start',
            value: 'public_field.value',
          },
          { text: 'ID de Credencial', value: 'id' },
          {
            text: 'Estado ',
            value: 'status',
            sortable: false,
            filterable: false,
          },
          {
            text: 'Acciónes',
            value: 'actions',
            sortable: false,
            filterable: false,
          },
        ],
      },

      dataTableStructure: [
        {
          key: 'allEntriesTable',
          entries: [],
          emptyTableMessage: 'viewCredential.noCredentials[0]',
        },
        {
          key: 'pendingTable',
          entries: [],
          emptyTableMessage: 'viewCredential.pending',
        },
        {
          key: 'approvedTable',
          entries: [],
          emptyTableMessage: 'viewCredential.approved',
        },
        {
          key: 'revokedTable',
          entries: [],
          emptyTableMessage: 'viewCredential.revoked',
        },
      ],

    }
  },
  computed: {
    ...mapState(useAccountStore, ['cid', 'caName', 'profilePhotoUrl', 'totalIssuedCredentials', 'allowedCredentials']),
    ...mapState(useTemplateStore, ['template']),
    templatePreview() {
      return this.template?.frontend_props?.preview
    },
    canIssueCred() {
      return this.totalIssuedCredentials >= this.allowedCredentials
    },
    getDesignType() {
      return this.template?.frontend_props?.design
    },
    getTemplateName() {
      return this.template?.frontend_props?.customTemplateName
    },
  },
  watch: {
    template() {
      this.getTemplate()
    },
  },
  created() {
    this.$debug(`*** ${this.$options.name} ***`)
  },
  mounted() {
    if (this.template?.name) {
      this.getTemplate()
    }
  },
  methods: {
    updateTable() {
      this.getTemplate()
    },
    closeSuccessModal() {
      this.updateTable()
      this.successModal = false
    },
    closeInBulkModal() {
      this.updateTable()
      this.shownInBulkModal = false
    },
    getTemplate() {
      logDebug('tid', this.tid)
      logDebug('tid', this.template)
      this.loadingTable = true
      // const template = this.template

      useAccountStore()[GET_TEMPLATE]({ tid: this.tid }).then((template) => {
        this.loadingTable = false
        if (!template) {
          return
        }
        this.revokedTable = []
        this.approvedTable = []
        logDebug('template', template)
        this.allEntriesTable = template.users.map(u => ({ ...u, status: [CredentialState.APPROVED, CredentialState.PENDING_APPROVAL, CredentialState.WAITING_WALLET].includes(u.status) ? CredentialState.APPROVED : u.status }))

        template.users.forEach((u) => {
          if (u.status === CredentialState.REVOKED) {
            this.revokedTable.push({ ...u, status: CredentialState.REVOKED })
          } else if ([CredentialState.APPROVED, CredentialState.PENDING_APPROVAL, CredentialState.WAITING_WALLET].includes(u.status)) {
            this.approvedTable.push({ ...u, status: CredentialState.APPROVED })
          }
        })

        if (template.users.length > 0) {
          logDebug(template.users[0].public_field.attr)

          this.credentialsHeaders[this.$i18n.locale][0] = {
            text: template.users[0].public_field.attr,
            align: 'start',
            value: 'public_field.value',
          }
        }
      })
    },
    closeIssueCertificate() {
      this.issueCertificateModal = false
    },
  },

}
</script>

<template>
  <v-container class="px-0">
    <v-row>
      <v-col cols="12" md="9" class="pr-16">
        <v-row>
          <v-col cols="12" class="pt-0">
            <v-img
              class="mt-5 mb-3"
              contain
              max-height="95"
              max-width="105"
              :src="templatePreview"
            />
            <h1 class="title_header">
              {{ template.name }}
            </h1>
          </v-col>

          <v-col cols="8" class="pt-4">
            <h2 class="subtitle_header">
              {{ $t('issue.pageTitle') }}
            </h2>
          </v-col>
          <v-col cols="4" class="pt-4 text-right">
            <v-btn
              v-if="false"
              :disabled="canIssueCred"
              class="next"
              @click="$router.push({ path: 'IssueCredentials' })"
            >
              <IconAdd />{{ $t('issue.new') }}
            </v-btn>
            <v-menu
              v-else
              bottom
              left
              :offset-y="true"
              class="dot-menu"
              content-class="dot-menu"
            >
              <template #activator="{ on }">
                <v-btn :disabled="canIssueCred" class="next" v-on="on">
                  <IconAdd />{{ $t('issue.new') }}
                </v-btn>
              </template>

              <v-list :ripple="false" class="dot-menu">
                <v-list-item :ripple="false" :to="{ path: 'IssueCredentials' }">
                  <v-list-item-title class="menu-title">
                    {{
                      $t('issue.menu[0]')
                    }}
                  </v-list-item-title>
                </v-list-item>
                <v-list-item disabled :ripple="false" :to="{ path: 'IssueInBulk' }">
                  <v-list-item-title class="menu-title">
                    {{
                      $t('issue.menu[1]')
                    }}
                  </v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>
          </v-col>
          <v-col cols="12" class="tables-view">
            <v-tabs v-model="tab" :show-arrows="false" fixed-tabs>
              <v-tab href="#tab-1" class="menu_text">
                {{ $t('viewCredential.tabs[0]') }} ({{ allEntriesTable.length }})
              </v-tab>
              <v-tab href="#tab-2" class="menu_text">
                {{ $t('viewCredential.tabs[1]') }} ({{ pendingTable.length }})
              </v-tab>
              <v-tab href="#tab-3" class="menu_text">
                {{ $t('viewCredential.tabs[2]') }} ({{ approvedTable.length }})
              </v-tab>
              <v-tab href="#tab-4" class="menu_text">
                {{ $t('viewCredential.tabs[3]') }} ({{ revokedTable.length }})
              </v-tab>
              <v-text-field
                v-model="search"
                class="search-table"
                append-icon="mdi-magnify"
                :label="$t('table.search')"
                single-line
                hide-details
              />
            </v-tabs>

            <v-tabs-items v-model="tab">
              <v-tab-item v-for="(dataTable, index) in dataTableStructure" :key="dataTable.key" :value="`tab-${index + 1}`">
                <DataTable
                  :key="dataTable.key"
                  :search="search"
                  :headers="credentialsHeaders"
                  :items="dataTable.entries"
                  :table-options="hasPendingApproval"
                  :loading="loadingTable"
                  @update-table="updateTable"
                >
                  <template #empty-table>
                    <v-card class="pa-8 dca-table">
                      <v-img
                        class="mx-auto"
                        max-height="99"
                        max-width="130"
                        :src="emptyTablePhoto"
                      />
                      <v-card-text class="strong_text pa-0 pt-2">
                        <p
                          class="light_text text-center mb-0 mx-auto"
                          style="max-width: 350px"
                        >
                          {{ $t(dataTable.emptyTableMessage) }}
                          <router-link
                            class="links"
                            style="font-size: 13px"
                            :to="{ path: 'IssueCredentials' }"
                          >
                            {{ $t('viewCredential.noCredentials[1]') }}
                          </router-link>
                          {{ $t('viewCredential.noCredentials[2]') }}
                        </p>
                      </v-card-text>
                    </v-card>
                  </template>
                </DataTable>
              </v-tab-item>
            </v-tabs-items>
          </v-col>
        </v-row>
      </v-col>
      <v-col cols="12" md="3" class="pt-14 pb-8 counters">
        <v-row class="pt-16">
          <v-col cols="12" class="pt-16 pb-2 px-0">
            <v-card>
              <v-card-text class="strong_text">
                <IconIssuedCredentials />
                <div>
                  <p>{{ allEntriesTable.length }}</p>
                  <span> {{ $t('dashboard.counters.issued') }} </span>
                </div>
              </v-card-text>
            </v-card>
          </v-col>
          <v-col cols="12" class="pt-2 pb-2 px-0">
            <v-card>
              <v-card-text class="strong_text">
                <IconRevokedCredentials />
                <div>
                  <p>{{ revokedTable.length }}</p>
                  <span> {{ $t('dashboard.counters.revoked') }} </span>
                </div>
              </v-card-text>
            </v-card>
          </v-col>
          <v-col cols="12" class="pt-2 pb-2 px-0">
            <v-card>
              <v-card-text class="strong_text">
                <IconApprovedCredentials />
                <div>
                  <p>{{ approvedTable.length }}</p>
                  <span> {{ $t('dashboard.counters.approved') }} </span>
                </div>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<style lang="scss">
.tables-view.col {
  .v-tabs-items {
    box-shadow: 0 2px 4px 0 var(--very-light-grey);
    margin-top: 35px;
  }

  .v-tabs {
    border-bottom: solid 1px var(--light-grey-blue);
    .v-tabs-bar {
      height: 38px;
      background-color: transparent;
      .v-tabs-slider {
        background-color: #0ea4b5;
      }
    }
    .v-tab {
      margin: 0;
      width: fit-content;
      padding: 0;
    }
  }
  .v-tabs-items {
    background-color: transparent;
  }
}
</style>
